<template>
  <div class="politica-privacidade-wrapper">
    <h1>Política de Privacidade</h1>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  data() {
    return {
      politica: [
        {
          id: 1,
          title: "Ponto de partida| Enquadramento",
          content:
            "<p>O respeito pela privacidade e confidencialidade de cada pessoa - que temos o privilégio de poder confiar nos nossos serviços - será, efetivamente, um dos baluartes por que regemos a nossa atividade clínica, no âmbito da Clínica de Psicologia sob a Direção Clínica de Ana Carolina Pereira. Acresce que a entrada em vigor do Regulamento Geral de Proteção de Dados 2016/679 e sua posterior aplicação obrigatória a partir do dia 25 de maio de 2018, veio robustecer esta mesma preocupação, pelo que a presente Política de Privacidade e Proteção de Dados, nos termos da nova legislação, afigura-se como um modo de sublinharmos e enfatizarmos o nosso compromisso com a privacidade de quem a nós recorre, i.e., dos nossos clientes. Assim, a nossa Política de Privacidade, que surge no quadro da recente legislação nacional (vulgo, RGPD) procurará dar a compreender a nossa identidade e prática clínica, bem como o tipo de conteúdos e informações que recolhemos (nas mais diversas valências clínicas e no âmbito do nossa Academia de formação), e o modo como os tratamos e salvaguardamos, as formas de os detentores de dados pessoais entrarem em contacto connosco e de exercer os seus direitos. Identificamos, ainda, questões relacionadas com a proteção legal de conteúdos do site. Procurará, ainda, responder às questões relacionadas com o período de conservação dos dados, o direito de acesso, retificação e atualização de dados, bem como a salvaguarda da utilização de dados por terceiros. A nossa premissa base é, sem dúvida, o respeito pela privacidade dos nossos utilizadores e gratidão pela confiança que depositam em nós.</p><p>Na verdade, o nosso compromisso para com quem confia nos nossos serviços não poderá, jamais, dissociar-se da explicitação, rigorosa e minuciosa, de todos os termos e condições por que nos regemos, para além da enumeração, cuidadosa, do modo como iremos salvaguardar a proteção dos dados e privacidade e confidencialidade dos nossos clientes. Valores sem os quais o exercício da prática clínica e formativa perderia o seu sentido.</p>"
        },
        {
          id: 2,
          title: "1. Nota prévia",
          content:
            '<p>Este site é propriedade intelectual de Ana Carolina Pereira, Clínica de Psicologia (doravante ACP – Clínica de Psicologia), sociedade por quotas Ana Carolina Pereira, Unipessoal Lda, pessoa colectiva com o NIPC 515375985, com sede na Rua Machado de Castro, 7, R/C 3000-254 Coimbra, distrito de Coimbra. Sublinha-se que o site pretende ser um meio de comunicação e divulgação informativa dos nossos serviços, não podendo os seus conteúdos ser entendidos como prestação de serviços.</p><p>Mais se esclarece, de acordo com o constante no Regulamento Geral de Proteção de Dados da Secretaria Geral da presidência do Conselho de Ministros, que o “Regulamento Geral de Proteção de Dados (RGPD) 2016/679 do Parlamento Europeu e do Conselho, que é o novo Regulamento Geral sobre a Proteção de Dados (RGPD) da União Europeia (UE), estabelece as regras relativas ao tratamento, por uma pessoa, uma empresa ou uma organização, de dados pessoais relativos a pessoas na UE. § Para além do reforço da proteção jurídica dos direitos dos titulares dos dados, o RGPD define novas regras e procedimentos do ponto de vista tecnológico”.</p><p>Ressalva-se que na presente Política de Privacidade e Proteção de Dados o plural e o termo "nós" refere-se a Ana Carolina Pereira, sobre quem impende responsabilidade civil na recolha e tratamento dos dados pessoais.</p>'
        },
        {
          id: 3,
          title: "2. Direitos de Propriedade Intelectual",
          content:
            "<p>Esclarece-se que o conteúdo deste site, incluindo ilustrações, marcas, desenhos, logótipos, conteúdo teórico-prático, imagens, materiais áudio e de vídeo, será propriedade de ACP - Clínica de Psicologia. Não será permitido reproduzir, distribuir, alterar, transmitir ou usar o conteúdo deste site para qualquer finalidade sem o prévio e expresso consentimento escrito para ACP – Clínica de Psicologia, sob pena de, se assim não suceder, se proceder às consequentes implicações jurídicas e legais.</p>"
        },
        {
          id: 4,
          title: "3. Reestruturação",
          content:
            "<p>ACP – Clínica de Psicologia reserva-se o direito de, sempre que assim se afigurar necessário, alterar os presentes Termos de Utilização. Em função disso, aconselhamos a sua consulta com regularidade. Acresce que poder-se-á proceder à reestruturação e modificação, a determinado momento e sem necessidade de pré-aviso ou comunicação, a configuração estrutural do site.</p>"
        },
        {
          id: 5,
          title: "4. Proteção de dados",
          content:
            "<p>Informa-se que os dados/informações pessoais recolhidos através do site serão tratados em conformidade com o estabelecido na Política de Privacidade e Política de Proteção de Dados Pessoais, com o Regulamento Geral de Proteção de Dados (EU 2016/679 do P.E. - RGPD) e com a Lei da Proteção de Dados Pessoais, Lei n.º 67/98, de 27 de outubro de 1998, alterada pela Lei n.º103/2015, 24 de agosto (“Lei de Proteção de Dados”).</p>"
        },
        {
          id: 6,
          title: "5. Responsabilidade pelo Tratamento",
          content:
            "<p>ACP – Clínica de Psicologia será a única entidade responsável pelo tratamento e recolha de dados pessoais no contexto dos serviços por si desempenhados. Salvaguardamos que não se partilhará quaisquer informações/dados com outras entidades, instituições ou empresas.</p>"
        },
        {
          id: 7,
          title: "6. Informações cedidas a terceiros",
          content:
            "<p>Garantimos, uma vez mais, que a privacidade dos nossos utilizadores é fundamental e premissa de inalienável importância para nós, pelo que não disponibilizaremos esses dados a terceiros. Seja como for, os seus dados pessoais poderão ser tratados por empresas, por nós, subcontratadas para o alojamento da página web, o alojamento das bases de dados para o envio de correio eletrónico ou para a resolução de problemas técnicos. A entidade a quem alocamos o alojamento das plataformas de dados é a PT-ISP|entidade reconhecida com idoneidade ética e legal. A sua respetiva política de privacidade poderá ser consultada no seu website onde se garante a total privacidade (por encriptação) dos dados dos seus utilizadores.</p><p>No que diz respeito à resolução de problemas técnicos, pontuais, dispomos de colaboradores fidelizados e procuramos sempre garantir – através de um sistema interno protocolar e administrativo – a salvaguarda da segurança de qualquer informação.</p>"
        }
      ]
    };
  },
  metaInfo() {
    return {
      title:
        "Política de Privacidade - Ana Carolina Pereira - Clínica de Psicologia",
      meta: [
        {
          name: "description",
          content: "Ana Carolina Pereira - Clínica de Psicologia"
        },
        { name: "robots", content: "index,follow" },
        { property: "og:site_name", content: "Ana Carolina Pereira" },
        { property: "og:type", content: "website" },
        {
          property: "og:title",
          content:
            "Política de Privacidade - Ana Carolina Pereira - Clínica de Psicologia"
        },
        {
          property: "og:description",
          content: "Ana Carolina Pereira - Clínica de Psicologia"
        }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquoise: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.politica-privacidade-wrapper {
  background-color: $white;
  height: auto;
  padding: $pd-desktop;

  @media only screen and (min-width: 1200px) and (max-width: 1919px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  @media only screen and (max-width: 599px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  h1 {
    margin-top: pxToEm(100);
    text-align: center;
    color: $turquoise;
  }
}
</style>




